import React, { useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import Modal from 'react-modal';
import { ReadOnlyContext } from '../App.js';
import Cookies from 'universal-cookie';

const UpdateTodoStatus = ({ _id, subject, duedate, description, sidenote, is_done }) => {

    const cookies = new Cookies();

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [updateState, setUpdateState] = useState('');

    const handleConfirm = async e => {
        e.preventDefault();
        setUpdateState('LOADING');
        let data = await fetch(`/api/todo/${_id}?key=${cookies.get('code')}`, {
            'method': 'PATCH',
            'mode': 'cors',
            'headers': {
                'content-type': 'application/json'
            },
            'body': JSON.stringify({
                'subject': subject,
                'duedate': duedate,
                'description': description,
                'sidenote': sidenote,
                'is_done': !is_done
            })
        });
        let response = await data.json();
        if(data.status === 200){
            setUpdateState('SUCCESS');
        }else if(data.status === 400){
            setUpdateState('ERROR_400');
        }else if(data.status === 403){
            setUpdateState('ERROR_403');
        };
    };

    // Handle modal open

    const handleModal = () => {
        if(readOnly){
            window.alert('You are currently in read-only mode!');
        }else{
            setModalIsOpen(true);
        };
    };

    let statusMessage;
    switch(updateState){
        case 'LOADING':
            statusMessage = <div>Loading...</div>;
            break;
        case 'SUCCESS':
            statusMessage = <div>Marked todo as {is_done ? 'ongoing' : 'done'}! <Link to={`/redirect?url=/todo/${_id}`} className="modal-redirect" onClick={() => setModalIsOpen(false)}>View updated todo</Link></div>;
            break;
        case 'ERROR_400':
            statusMessage = <div>Unknown error! Check console for details</div>;
            break;
        case 'ERROR_403':
            statusMessage = <div>You are in read-only mode! Please try again with a valid access token.</div>
    };

    return (
        <div>
            <div>
                <button className="button-main" onClick={handleModal}>Mark todo as {is_done ? 'ongoing' : 'complete'}</button>
            </div>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div className="close-modal">
                    <button onClick={() => setModalIsOpen(false)}>X</button>
                </div>
                <div className="modal-title">
                    Are you sure you want to mark the following todo as {is_done ? 'ongoing' : 'complete'}?
                </div>
                <div className="modal-list">
                    <ul>
                        <li>Subject: {subject}</li>
                        <li>Due date: {duedate}</li>
                        <li>Description: {description}</li>
                        <li>Side notes: {sidenote || 'None'}</li>
                    </ul>
                </div>
                <div>
                    <button className="button-main" onClick={e => handleConfirm(e)}>Confirm</button>
                    <button className="button-main" type="submit" onClick={() => {setModalIsOpen(false);}}>Cancel</button>
                </div>
                <div className="modal-status">
                    {statusMessage}
                </div>
            </Modal>
        </div>
    );
};

export default UpdateTodoStatus;
