import React, { useState, useContext } from 'react';
import Modal from 'react-modal';
import { Link } from 'react-router-dom';
import Cookies from 'universal-cookie';
import { ReadOnlyContext } from '../App.js';

Modal.setAppElement('#root');

const AddTodo = () => {

    const cookies = new Cookies();

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [todo, setTodo] = useState({
        'subject': '',
        'duedate': '',
        'description': '',
        'sidenote': ''
    });
    const [creationState, setCreationState] = useState('');

    // Handle form submission

    const handleSubmit = async e => {
        e.preventDefault();
        setCreationState('LOADING');
        let data = await fetch(`/api/todo?key=${cookies.get('code')}`, {
            'method': 'POST',
            'mode': 'cors',
            'headers': {
                'content-type': 'application/json'
            },
            'body': JSON.stringify(todo)
        });
        let response = await data.json();
        if(data.status === 200){
            setCreationState('SUCCESS');
            setTodo(prevTodo => ({...prevTodo, _id: response._id}));
        }else if(data.status === 400){
            setCreationState('ERROR_400');
        }else if(data.status === 403){
            setCreationState('ERROR_403');
        };
    };

    // Handle modal open

    const handleModal = () => {
        if(readOnly){
            window.alert('You are currently in read-only mode!');
        }else{
            setModalIsOpen(true);
        };
    };

    // Handle input changes

    const handleInput = (e, field) => {
        e.persist();
        e.target.style.height = "";
        e.target.style.height = e.target.scrollHeight + "px";
        setTodo(prevTodo => ({...prevTodo, [field]: e.target.value}))
    };

    // Render status message

    let statusMessage;
    switch(creationState){
        case 'LOADING':
            statusMessage = <div>Loading...</div>;
            break;
        case 'SUCCESS':
            statusMessage = <div>Successfully added todo! <Link to={`/todo/${todo._id}`} className="modal-redirect" onClick={() => setModalIsOpen(false)}>View created todo</Link></div>;
            break;
        case 'ERROR_400':
            statusMessage = <div>Please fill in all the required fields!</div>;
            break;
        case 'ERROR_403':
            statusMessage = <div>You are in read-only mode! Please try again with a valid access token.</div>
    };

    return (
        <div>
            <Link to="#" onClick={handleModal}>Add Todo</Link>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div>
                    <div className="close-modal"><button onClick={() => {if(!readOnly) setModalIsOpen(false)}}>X</button></div>
                    <div className="modal-title">
                        Add todo
                    </div>
                    <form className="modal-form">
                        <div>
                            <label htmlFor="subject">*Subject: </label>
                            <input type="text" value={todo.subject} onChange={e => handleInput(e, 'subject')} name="subject" id="subject"></input>
                        </div>
                        <div>
                            <label htmlFor="duedate">*Due date: </label>
                            <input type="date" min={new Date().toISOString().split("T")[0]} value={todo.duedate} onChange={e => handleInput(e, 'duedate')} name="duedate" id="duedate"></input>
                        </div>
                        <div>
                            <label htmlFor="description">*Description: </label>
                            <textarea value={todo.description} onChange={e => handleInput(e, 'description')} name="description" id="description"></textarea>
                        </div>
                        <div>
                            <label htmlFor="sidenote">Sidenotes: </label>
                            <textarea value={todo.sidenote} onChange={e => handleInput(e, 'sidenote')} name="sidenote" id="sidenote"></textarea>
                        </div>
                        <button className="button-main" type="submit" onClick={e => handleSubmit(e)}>Submit</button>
                    </form>
                    <div className="modal-status">
                        {statusMessage}
                    </div>
                </div>
            </Modal>
        </div>
    );
};


export default AddTodo;
