import React, { useState, useContext } from 'react';
import Modal from 'react-modal';
import { Link } from 'react-router-dom';
import { ReadOnlyContext } from '../App.js';
import Cookies from 'universal-cookie';

const RemoveNote = ({ classID, _id, title, content, resource, sidenote, date }) => {

    const cookies = new Cookies();

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [deletionState, setDeletionState] = useState('');

    // Handle form submission

    const handleSubmit = async e => {
        e.preventDefault();
        setDeletionState('LOADING');
        let data = await fetch(`/api/note/${classID}/${_id}?key=${cookies.get('code')}`, {
            'method': 'DELETE',
            'mode': 'cors'
        });
        let response = await data.json();
        if(data.status === 200){
            setDeletionState('SUCCESS');
        }else if(data.status === 400){
            setDeletionState('ERROR_400');
        }else if(data.status === 403){
            setDeletionState('ERROR_403');
        };
    };

    // Handle modal open

    const handleModal = () => {
        if(readOnly){
            window.alert('You are currently in read-only mode!');
        }else{
            setModalIsOpen(true);
        };
    };

    // Handle status message

    let statusMessage;
    switch(deletionState){
        case 'LOADING':
            statusMessage = <div>Loading...</div>;
            break;
        case 'SUCCESS':
            statusMessage = <div>Successfully removed note! <Link to={`/class/${classID}`} className="modal-redirect" onClick={() => setModalIsOpen(false)}>Go back to class</Link></div>;
            break;
        case 'ERROR_400':
            statusMessage = <div>Unknown error! Check console for details</div>;
            break;
        case 'ERROR_403':
            statusMessage = <div>You are in read-only mode! Please try again with a valid access token.</div>
    };

    return (
        <div>
            <button className="button-main" onClick={handleModal}>Remove this note</button>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div className="close-modal">
                    <button onClick={() => setModalIsOpen(false)}>X</button>
                </div>
                <div className="modal-title">
                    Are you sure you want to remove the following note?
                </div>
                <div className="modal-list">
                    <ul>
                        <li><strong>Title:</strong> {title}</li>
                        <li><strong>Content:</strong> {content}</li>
                        <li><strong>Resources:</strong> {resource}</li>
                        <li><strong>Side notes:</strong> {sidenote}</li>
                        <li><strong>Date created:</strong> {date && date.split('T')[0]}</li>
                    </ul>
                </div>
                <div>
                    <button className="button-main" type="submit" onClick={e => handleSubmit(e)}>Confirm</button>
                    <button className="button-main" onClick={() => setModalIsOpen(false)}>Cancel</button>
                </div>
                <div className="modal-status">
                    {statusMessage}
                </div>
            </Modal>
        </div>
    );
};

export default RemoveNote;
