import React, { useState, useEffect, useContext } from 'react';
import Modal from 'react-modal';
import { Link } from 'react-router-dom';
import { ReadOnlyContext } from '../App.js';
import Cookies from 'universal-cookie';

const EditNote = (props) => {

    const cookies = new Cookies();

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [note, setNote] = useState(props);
    const [editState, setEditState] = useState('');

    useEffect(() => {
        setNote(props)
    }, [props]);

    // Handle input change

    const handleInput = (e, field) => {
        e.persist();
        e.target.style.height = "";
        e.target.style.height = e.target.scrollHeight + "px";
        setNote(prevNote => ({...prevNote, [field]: e.target.value}))
    };

    // Handle modal open

    const handleModal = () => {
        if(readOnly){
            window.alert('You are currently in read-only mode!');
        }else{
            setModalIsOpen(true);
        };
    };

    // Handle form submission

    const handleSubmit = async e => {
        e.preventDefault();
        setEditState('LOADING');
        let data = await fetch(`/api/note/${props.classID}/${props._id}?key=${cookies.get('code')}`, {
            'method': 'PATCH',
            'mode': 'cors',
            'headers': {
                'content-type': 'application/json'
            },
            'body': JSON.stringify(note)
        });
        let response = await data.json();
        if(data.status === 200){
            setEditState('SUCCESS');
        }else if(data.status === 400){
            setEditState('ERROR_400');
        }else if(data.status === 403){
            setEditState('ERROR_403');
        };
    };

    // Handle status message

    let statusMessage;
    switch(editState){
        case 'LOADING':
            statusMessage = <div>Loading...</div>;
            break;
        case 'SUCCESS':
            statusMessage = <div>Successfully edited note! <Link to={`/redirect?url=/class/${props.classID}/${props._id}`} className="modal-redirect" onClick={() => setModalIsOpen(false)}>View updated note</Link></div>;
            break;
        case 'ERROR_400':
            statusMessage = <div>Please fill in all the required fields!</div>;
            break;
        case 'ERROR_403':
            statusMessage = <div>You are in read-only mode! Please try again with a valid access token.</div>
    };

    return (
        <div>
            <button className="button-main" onClick={handleModal}>Edit this note</button>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div className="close-modal">
                    <button onClick={() => setModalIsOpen(false)}>X</button>
                </div>
                <div className="modal-title">
                    Edit note
                </div>
                <div>
                    <form className="modal-form">
                        <div>
                            <label htmlFor="title">*Title: </label>
                            <input type="text" value={note.title} name="title" id="title" onChange={e => handleInput(e, 'title')}></input>
                        </div>
                        <div>
                            <label htmlFor="content">*Content: </label>
                            <textarea value={note.content} name="content" id="content" onChange={e => handleInput(e, 'content')}></textarea>
                        </div>
                        <div>
                            <label htmlFor="resource">Extra resources: </label>
                            <textarea value={note.resource} name="resource" id="resource" onChange={e => handleInput(e, 'resource')}></textarea>
                        </div>
                        <div>
                            <label htmlFor="sidenote">Side notes: </label>
                            <textarea value={note.sidenote} name="sidenote" id="sidenote" onChange={e => handleInput(e, 'sidenote')}></textarea>
                        </div>
                        <button className="button-main" type="submit" onClick={e => handleSubmit(e)}>Submit</button>
                    </form>
                    <div className="modal-status">
                        {statusMessage}
                    </div>
                </div>
            </Modal>
        </div>
    );
};

export default EditNote;
