import React, { useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import Modal from 'react-modal';
import { ReadOnlyContext } from '../App.js';
import Cookies from 'universal-cookie';

const RemoveTodo = ({ _id, subject, duedate, description, sidenote }) => {

    const cookies = new Cookies();

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [deletionState, setDeletionState] = useState('');

    // Handle form submission

    const handleConfirm = async e => {
        e.preventDefault();
        setDeletionState('LOADING');
        let data = await fetch(`/api/todo/${_id}?key=${cookies.get('code')}`, {
            'method': 'DELETE',
            'mode': 'cors'
        });
        let response = await data.json();
        if(data.status === 200){
            setDeletionState('SUCCESS');
        }else if(data.status === 400){
            setDeletionState('ERROR_400');
        }else if(data.status === 403){
            setDeletionState('ERROR_403');
        };
    };

    // Handle modal open

    const handleModal = () => {
        if(readOnly){
            window.alert('You are currently in read-only mode!');
        }else{
            setModalIsOpen(true);
        };
    };

    // Handle status message

    let deletionStatus;
    switch(deletionState){
        case 'LOADING':
            deletionStatus = <div>Loading...</div>;
            break;
        case 'SUCCESS':
            deletionStatus = <div>Successfully removed todo! <Link to="/todo" className="modal-redirect" onClick={() => setModalIsOpen(false)}>Go back to todos list</Link></div>;
            break;
        case 'ERROR_400':
            deletionStatus = <div>Unknown error! Check console for details</div>;
            break;
        case 'ERROR_403':
            deletionStatus = <div>You are in read-only mode! Please try again with a valid access token.</div>
    };


    return (
        <div>
            <div>
                <button className="button-main" onClick={handleModal}>Remove Todo</button>
            </div>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div className="close-modal">
                    <button onClick={() => setModalIsOpen(false)}>X</button>
                </div>
                <div className="modal-title">
                    Are you sure you want to remove the following todo?
                </div>
                <div className="modal-list">
                    <ul>
                        <li><strong>Subject:</strong> {subject}</li>
                        <li><strong>Due date:</strong> {duedate}</li>
                        <li><strong>Description:</strong> {description}</li>
                        <li><strong>Side notes:</strong> {sidenote || 'None'}</li>
                    </ul>
                </div>
                <div>
                    <button className="button-main" onClick={e => handleConfirm(e)}>Confirm</button>
                    <button className="button-main" type="submit" onClick={() => {setModalIsOpen(false);}}>Cancel</button>
                </div>
                <div className="modal-status">
                    {deletionStatus}
                </div>
            </Modal>
        </div>
    );
};

export default RemoveTodo;
